"use client";
import { useState } from "react";
import { KpiCard } from "@/components/admin/kpi-card";
import { createClient } from "@supabase/supabase-js";

const supabaseUrl = process.env.NEXT_PUBLIC_SUPABASE_URL as string;
const supabaseAnonKey = process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY as string;
const supabase = createClient(supabaseUrl, supabaseAnonKey);

interface LostItem {
  name: string;
  dateLost: string;
  securityQuestion: string;
}

export default function DashboardPage() {
  const [lostItem, setLostItem] = useState<LostItem>({
    name: "",
    dateLost: "",
    securityQuestion: "",
  });

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    setLostItem({ ...lostItem, [name]: value });
  };

  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    const { data, error } = await supabase
      .from("lost_items")
      .insert([{ ...lostItem }]);

    if (error) {
      console.error("Error inserting data:", error);
    } else {
      console.log("Data inserted successfully:", data);
      setLostItem({ name: "", dateLost: "", securityQuestion: "" }); // Reset form
    }
  };

  return (
    <div className="space-y-6">
      <section aria-labelledby="overview" className="space-y-2">
        <h1 id="overview" className="text-balance text-2xl font-semibold tracking-tight md:text-3xl">
          Dashboard Overview
        </h1>
        <p className="text-sm text-muted-foreground">Quick snapshot of your key metrics.</p>
      </section>

      <section className="grid gap-4 sm:grid-cols-2 lg:grid-cols-2">
        <KpiCard title="Total Revenue" value="$128,420" trend="+12.4%" trendTone="positive" /> 
        <KpiCard title="Active Users" value="24,581" trend="+5.2%" trendTone="positive" />
      </section>

      <section className="grid gap-4 lg:grid-cols-3">
        <form onSubmit={handleSubmit} className="space-y-4">
          <div>
            <label htmlFor="name" className="block text-sm font-medium">Item Name</label>
            <input
              type="text"
              name="name"
              id="name"
              value={lostItem.name}
              onChange={handleChange}
              required
              className="mt-1 block w-full border border-gray-300 rounded-md p-2"
            />
          </div>
          <div>
            <label htmlFor="dateLost" className="block text-sm font-medium">Date Lost</label>
            <input
              type="date"
              name="dateLost"
              id="dateLost"
              value={lostItem.dateLost}
              onChange={handleChange}
              required
              className="mt-1 block w-full border border-gray-300 rounded-md p-2"
            />
          </div>
          <div>
            <label htmlFor="securityQuestion" className="block text-sm font-medium">Security Question</label>
            <select
              name="securityQuestion"
              id="securityQuestion"
              value={lostItem.securityQuestion}
              onChange={handleChange}
              required
              className="mt-1 block w-full border border-gray-300 rounded-md p-2"
            >
              <option value="">Select a question</option>
              <option value="What is your pet's name?">What is your pet's name?</option>
              <option value="What is your mother's maiden name?">What is your mother's maiden name?</option>
              <option value="What was the name of your first school?">What was the name of your first school?</option>
            </select>
          </div>
          <button type="submit" className="mt-4 bg-blue-500 text-white rounded-md p-2">Submit</button>
        </form>
      </section>
    </div>
  );
}